.list{
    color: #fff;
    width: 300px;
    background: #023e8a;
    height: 50px;
margin: 20px auto;
border-radius: 5px;
margin-bottom: 20px;
}
.list p{
    padding: 10px;
    position: relative;
    color: #fff;
    font-weight: 400;
    text-align: left;
}
.list p input{
    background: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;

 }

 .list p input:focus {
     outline: none;
 }
.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons{
    margin: 0 5px;
font-size: 15px;
}