body {
  background: #48cae4;
 
}
.App {
  background: #00b4d8;
  text-align: center;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  padding: 10px 0;
}
.display-5 {
  color : #03045e;
  margin:20px auto;
  font-weight: bold;
}
#todo-form input{
  background: #ade8f4;
  border: 0;
  width: 220px;
  height: 50px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  color: black;
  margin: 20px;

}
#todo-form input::placeholder {
  color:#9c948d;
}
#todo-form input:focus, #todo-form button:focus{
  outline: none;
}
#todo-form button {
  background:  #48cae4;
  width: 80px;
  height: 50px;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold ;
  color: black
  
}